import React, { Component } from 'react';
import './App.css';
import Cloth from './Cloth.js';

class Travis extends Component {
  constructor(props) {
      super(props);
      this.colors = ['orange', 'pink', 'limegreen', 'lightblue', 'red'];
      this.state = {
        color: 'black',
      }
  }
  getColor() {
    let color = this.colors[Math.floor(Math.random() * this.colors.length)];
    console.log(color);
    return color;
  }

  newColor() {
    this.setState({color: this.getColor()});
  }

  componentDidMount() {
    this.newColor();
  }

  render() {
    return (
      <div className="Travis" style={{color:this.state.color}}>
        <h1>Travis Newgren<span>&trade;</span></h1>
        <p>
          hi! here's some of my work. email me at <a href='mailto:travisnewgren@gmail.com'>travisnewgren<span>@</span>gmail.com</a> if you want to say hi. <button onClick={this.newColor.bind(this)}>CLICK HERE</button> to change colors. I like front end design and webwork*. blah blah crossword puzzle. I live in Chicago.
        </p>
        <h2>drag and drop clothing &darr;</h2>
        <Cloth style={{marginTop: '5vh'}}/>
        <h2>more coming soon   &darr;</h2>

      </div>
    );
  }
}

export default Travis;
